const modal = document.querySelector('.modal');
const modalContent = modal.querySelector('.modal-content');
const closeButton = modal.querySelector('.js-modal-close');

// Функція, щоб відкрити модальне вікно
function openModal() {
  modal.style.display = 'block';
}

// Функція, щоб закрити модальне вікно
function closeModal() {
  modal.style.display = 'none';
}

// Додати подію "click" для відкриття модального вікна
document.querySelector('.book-cover').addEventListener('click', openModal);

// Додати подію "click" для закриття модального вікна по хрестику
closeButton.addEventListener('click', closeModal);

// Додати подію "click" для закриття модального вікна по backdrop
modalContent.addEventListener('click', event => {
  if (event.target === modalContent) {
    closeModal();
  }
});

// Додати подію для закриття модального вікна на клавішу "ESC"
document.addEventListener('keydown', event => {
  if (event.key === 'Escape') {
    closeModal();
  }
});
